/* Workspace Area Container */
.workspace-area-container {
  flex: 1;
  background-color: #f8f9fa;
  position: relative;
  overflow: hidden;
  user-select: none;
}

/* Empty State Styling */
.workspace-empty-state {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100%;
  color: #6c757d;
  text-align: center;
  padding: 20px;
}

.workspace-title {
  font-size: 18px;
  font-weight: 500;
  margin-bottom: 8px;
  color: #495057;
}

.workspace-subtitle {
  font-size: 14px;
  font-weight: 400;
  margin-bottom: 16px;
}

.workspace-instructions {
  font-size: 12px;
  color: #adb5bd;
  max-width: 300px;
  line-height: 1.4;
}

/* Base Widget Styling */
.workspace-widget {
  position: absolute;
  background-color: #ffffff;
  border: 1px solid #e9ecef;
  border-radius: 8px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
  overflow: hidden;
  cursor: move;
  transition: box-shadow 0.2s ease, transform 0.1s ease;
  box-sizing: border-box;
}

.workspace-widget:hover {
  box-shadow: 0 4px 16px rgba(0, 0, 0, 0.15);
}

.workspace-widget:active {
  cursor: grabbing;
}

/* Widget Type Specific Styling */
.workspace-widget-charts {
  border-left: 4px solid #007bff;
}

.workspace-widget-charts .widget-header {
  background: linear-gradient(135deg, #007bff, #0056b3);
}

.workspace-widget-positions {
  border-left: 4px solid #28a745;
}

.workspace-widget-positions .widget-header {
  background: linear-gradient(135deg, #28a745, #1e7e34);
}

.workspace-widget-watchlist {
  border-left: 4px solid #ffc107;
}

.workspace-widget-watchlist .widget-header {
  background: linear-gradient(135deg, #ffc107, #e0a800);
}

.workspace-widget-trades {
  border-left: 4px solid #dc3545;
}

.workspace-widget-trades .widget-header {
  background: linear-gradient(135deg, #dc3545, #c82333);
}

/* Widget Header */
.widget-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 12px 16px;
  color: #ffffff;
  font-weight: 500;
  cursor: move;
  user-select: none;
}

.widget-title {
  display: flex;
  align-items: center;
  gap: 8px;
  font-size: 14px;
}

.widget-icon {
  font-size: 16px;
}

.widget-controls {
  display: flex;
  gap: 4px;
}

.widget-control-btn {
  background: none;
  border: none;
  color: #ffffff;
  font-size: 16px;
  font-weight: bold;
  cursor: pointer;
  padding: 2px 6px;
  border-radius: 4px;
  transition: background-color 0.2s ease;
  display: flex;
  align-items: center;
  justify-content: center;
  min-width: 20px;
  height: 20px;
}

.widget-control-btn:hover {
  background-color: rgba(255, 255, 255, 0.2);
}

.widget-control-btn:active {
  background-color: rgba(255, 255, 255, 0.3);
}

/* Widget Content */
.widget-content {
  padding: 16px;
  color: #495057;
  font-size: 13px;
  line-height: 1.5;
  height: calc(100% - 60px);
  overflow: auto;
  background-color: #ffffff;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
}

/* Resize Handles - All corners and edges */
.widget-resize-handle {
  position: absolute;
  width: 12px;
  height: 12px;
  background: rgba(73, 80, 87, 0.3);
  border: 1px solid rgba(73, 80, 87, 0.5);
  border-radius: 2px;
  transition: background-color 0.2s ease;
}

.widget-resize-handle:hover {
  background: rgba(73, 80, 87, 0.6);
  border-color: rgba(73, 80, 87, 0.8);
}

/* Corner handles */
.widget-resize-handle-nw {
  top: 0;
  left: 0;
  cursor: nw-resize;
}

.widget-resize-handle-ne {
  top: 0;
  right: 0;
  cursor: ne-resize;
}

.widget-resize-handle-sw {
  bottom: 0;
  left: 0;
  cursor: sw-resize;
}

.widget-resize-handle-se {
  bottom: 0;
  right: 0;
  cursor: se-resize;
}

/* Edge handles */
.widget-resize-handle-n {
  top: 0;
  left: 50%;
  transform: translateX(-50%);
  cursor: n-resize;
}

.widget-resize-handle-s {
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
  cursor: s-resize;
}

.widget-resize-handle-w {
  left: 0;
  top: 50%;
  transform: translateY(-50%);
  cursor: w-resize;
}

.widget-resize-handle-e {
  right: 0;
  top: 50%;
  transform: translateY(-50%);
  cursor: e-resize;
}

/* Widget States */
.workspace-widget.dragging {
  opacity: 0.8;
  transform: scale(1.02);
  z-index: 1000;
}

.workspace-widget.resizing {
  z-index: 1000;
}

/* Responsive Design */
@media (max-width: 768px) {
  .workspace-widget {
    min-width: 280px;
    min-height: 200px;
  }
  
  .widget-header {
    padding: 10px 12px;
  }
  
  .widget-title {
    font-size: 13px;
  }
  
  .widget-content {
    padding: 12px;
    font-size: 12px;
  }
  
  .workspace-instructions {
    font-size: 11px;
    max-width: 250px;
  }
}

/* Animation for widget appearance */
@keyframes widgetAppear {
  from {
    opacity: 0;
    transform: scale(0.8) translateY(20px);
  }
  to {
    opacity: 1;
    transform: scale(1) translateY(0);
  }
}

.workspace-widget {
  animation: widgetAppear 0.3s ease-out;
}

/* Focus states for accessibility */
.workspace-widget:focus-within {
  outline: 2px solid #007bff;
  outline-offset: 2px;
}

.widget-control-btn:focus {
  outline: 2px solid #ffffff;
  outline-offset: 2px;
}

/* Custom scrollbar for widget content */
.widget-content::-webkit-scrollbar {
  width: 6px;
}

.widget-content::-webkit-scrollbar-track {
  background: #f1f3f4;
  border-radius: 3px;
}

.widget-content::-webkit-scrollbar-thumb {
  background: #c1c1c1;
  border-radius: 3px;
}

.widget-content::-webkit-scrollbar-thumb:hover {
  background: #a8a8a8;
}
